import axios, { type AxiosInstance, type AxiosRequestConfig } from "axios";
import { ElMessage } from "element-plus";
import { get, merge } from "lodash-es";
import { getCookie, getToken } from "./cache/cookies";
import router from "@/router";

/**  */
function logout() {
  // location.reload();
  router.push({
    path: '/login'
  })
}

/**  */
function createService() {
  //  axios  service
  const service = axios.create();
  // 
  service.interceptors.request.use(
    (config) => config,
    // 
    (error) => Promise.reject(error)
  );
  // 
  service.interceptors.response.use(
    (response) => {
      // apiData  api 
      const apiData = response.data;
      //  code  code
      const code = apiData.errorCode;
      if (code === 10000) {
        // Token 
        return logout();
      } else if (!apiData.success) {
        ElMessage.error(apiData.errorMessage);
        return apiData;
      } else {
        return apiData;
      }
    },
    (error) => {
      // status  HTTP 
      const status = get(error, "response.status");
      switch (status) {
        case 400:
          error.message = "";
          break;
        case 401:
          // Token 
          logout();
          break;
        case 403:
          error.message = "";
          break;
        case 404:
          error.message = "";
          break;
        case 408:
          error.message = "";
          break;
        case 500:
          error.message = "";
          break;
        case 501:
          error.message = "";
          break;
        case 502:
          error.message = "";
          break;
        case 503:
          error.message = "";
          break;
        case 504:
          error.message = "";
          break;
        case 505:
          error.message = "HTTP ";
          break;
        default:
          break;
      }
      ElMessage.error(error.message);
      return Promise.reject(error);
    }
  );
  return service;
}

let baseURL = "http://micro-serve.missxiaolin.com"
const urls = ['127.0.0.1', "localhost", "mic.missxiaolin.com", "micro-adm-h.missxiaolin.com"]

if (urls.indexOf(window.location.hostname) > -1) {
  baseURL = "http://127.0.0.1:9002";
}

export {
  baseURL
}

/**  */
function createRequest(service: AxiosInstance) {
  return function <T>(config: AxiosRequestConfig): Promise<T> {
    const token = getToken();
    const defaultConfig = {
      headers: {
        //  Token
        Authorization: token ? `${token}` : undefined,
        MonitorAppId: getCookie("PROJECT_ID"),
        "Content-Type": "application/json",
      },
      timeout: 5000,
      baseURL: baseURL,
      data: {},
    };
    //  defaultConfig  config  mergeConfig
    const mergeConfig = merge(defaultConfig, config);
    return service(mergeConfig);
  };
}

/**  */
const service = createService();
/**  */
export const request = createRequest(service);
