import router from "@/router";
import store from "@/store";
import microApp from "@micro-zoe/micro-app";

/**
 * @method : 
 * 1       ----> false
 * 2    ----> true
 */
export const verifySwitch = process.env.NODE_ENV === "production";

/**
 * 
 * @param {Boolean} isMainServe 
 * @param {Object} currentService 
 * @param {String} fullPath 
 * @param {Function} menuStore store
 * @param {String} pageName 
 * @param {Function} callBack 
 * @returns
 */
export const setServiceBasicInfo = ({
  isMainServe,
  currentService,
  fullPath,
  menuStore,
  pageName = "",
  callBack,
  microStore,
}: any) => {
  // url  
  // TODO:      -->  404
  if (!isMainServe && !Object.keys(currentService).length) {
    // return router.push({ path: "/error/404" });
  }
  // 
  if (callBack && typeof callBack === "function") {
    callBack();
  }
  setTimeout(() => {
    store.dispatch("setCurrentService", {
      isMainServe,
      currentService,
      fullPath,
    });
  }, 0);
  setTimeout(() => {
    //   
    //  microStore   
    if (!isMainServe) {
      microApp.setData(currentService.name, {
        path: fullPath,
        openBlank: currentService.openBlank,
        data: new Date().getTime()
      });
    }
  }, 10);
};
