import { request } from "@/utils/service";

/**
 * 
 * @returns 
 */
export function getColum(data: any) {
  return request<any>({
    url: "/adm/get/colum",
    method: "post",
    data
  });
}

/**
 * 
 * @returns 
 */
export function getMicro() {
  return request<any>({
    url: "/adm/get/micro",
    method: "get",
  });
}